.about {
  padding: 0;
}

.about__grid-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(4, min-content);
  gap: 0 20px;
  font-family: var(--font-family);
}

.about__grid-item-title {
  grid-column: 1/7;
  grid-row: 1/2;
  padding-top: 11.5vw;
}

.about__grid-item-title-h2 {
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
}

.about__grid-item-title-p {
  padding-top: 4vw;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: clamp(30px, 2.5vw, 34px);
  line-height: 150%;
  letter-spacing: 0.02em;
}

.about__text {
  font-weight: 300;
  font-size: clamp(16px, 1.5vw, 18px);
  line-height: 150%;
}

.about__text-small-a {
  max-width: 280px;
  grid-column: 4/7;
  grid-row: 2/3;
  padding-top: 4vw;
  justify-self: end;
}

.about__text-small-b {
  width: 289px;
  justify-self: center;
  grid-column: 1/6;
  grid-row: 4/5;
  margin-bottom: 7.5vw;
}

.about__picture {
  grid-column: 1/13;
  grid-row: 1/5;
}

.about__picture-list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(3, min-content);
  gap: 20px 20px;
}

.about__picture-list-item-a {
  grid-column: 9/13;
  grid-row: 1/2;
  transform: translateY(-80px);
  max-height: 700px;
}

.about__picture-list-item-b {
  grid-column: 1/6;
  grid-row: 2/3;
  max-height: 313px;
  max-width: 480px;
  transform: translateY(103px);
}

.about__picture-list-item-c {
  grid-column: 6/9;
  grid-row: 2/3;
  max-height: 377px;
  transform: translateY(228px);
}

.about__picture-list-item-d {
  grid-column: 9/13;
  grid-row: 2/3;
  max-height: 507px;
}

.about__picture-list-item-e {
  grid-column: 10/13;
  grid-row: 3/4;
  padding-top: 80px;
}

.about__picture-swiper {
  display: none;
}

@media (max-width: 1024px) {
  .about__grid-item-title {
    padding-top: 11.7vw;
    grid-column: 1/8;
  }
  .about__grid-item-title-p {
    font-size: clamp(20px, 3vw, 30px);
  }
  .about__text {
    font-size: clamp(16px, 2vw, 18px);
  }
  .about__text-small-a {
    padding-top: 6vw;
    grid-column: 1/8;
    justify-self: end;
  }
  .about__text-small-b {
    margin-bottom: 3.5vw;
    grid-column: 1/5;
  }
  .about__picture-list-item-a {
    transform: translateY(-56px);
  }
  .about__picture-list-item-b {
    max-height: 247px;
    transform: translateY(101px);
  }
  .about__picture-list-item-c {
    max-height: 297px;
    transform: translateY(168px);
  }
  .about__picture-list-item-d {
    max-height: 401px;
  }
  .about__picture-list-item-e {
    padding-top: 60px;
  }
}
@media (max-width: 768px) {
